import React, { useContext, useCallback } from 'react';
import { _ } from '@splunk/ui-utils/i18n';
import { TelemetryContext } from '@splunk/dashboard-telemetry';
import Remove from '@splunk/dashboard-icons/Remove';
import ActionButton from './ActionButton';
import type { BaseButtonProps } from './BaseButton';

const DeleteButton = ({
    itemId,
    dashboardApi,
    screenReaderText,
}: BaseButtonProps): JSX.Element | null => {
    const telemetry = useContext(TelemetryContext);

    const handleOnClick = useCallback(() => {
        if (!dashboardApi || !itemId) {
            return;
        }
        telemetry.collect({
            source: 'actionMenu',
            event: 'delete',
        });
        dashboardApi.removeVisualizations([itemId]);
    }, [telemetry, dashboardApi, itemId]);

    if (itemId == null || dashboardApi == null) {
        return null;
    }
    return (
        <ActionButton
            data-test="delete-button"
            onClick={handleOnClick}
            icon={<Remove screenReaderText={screenReaderText || _('Delete')} />}
        />
    );
};

export default DeleteButton;
