import React, { useMemo, useContext } from 'react';
import debounce from 'lodash/debounce';
import Download from '@splunk/dashboard-icons/Download';
import { TelemetryContext } from '@splunk/dashboard-telemetry';
import { _ } from '@splunk/ui-utils/i18n';
import get from 'lodash/get';
import { exportVizAsPng } from '@splunk/dashboard-utils/ContentExporter';
import { TOAST_TYPES } from '@splunk/react-toast-notifications/ToastConstants';
import { BaseButtonProps } from './BaseButton';
import ActionButton from './ActionButton';

interface ExportButtonProps extends BaseButtonProps {
    createToast: (config: { message: string; type: string }) => void;
}

const ExportButton = ({
    itemId,
    visualization,
    dashboardApi,
    screenReaderText,
    createToast,
}: ExportButtonProps): JSX.Element | null => {
    const telemetry = useContext(TelemetryContext);
    const type = get(visualization, 'type', null);

    const handleOnClick = useMemo(
        () =>
            debounce(async () => {
                if (!dashboardApi || !itemId) {
                    return;
                }
                try {
                    await exportVizAsPng({
                        vizDomNode: dashboardApi.getVisualizationDomElement(
                            itemId
                        ),
                        vizId: itemId,
                        vizType: type,
                        scale: 1.5,
                        showToast: (message: string): void => {
                            createToast({
                                message,
                                type: TOAST_TYPES.ERROR,
                            });
                        },
                    });
                    telemetry.emit({
                        pageAction: 'udf.export_viz',
                        source: 'actionMenu',
                        event: 'viz.export_success',
                        metadata: { type },
                    });
                } catch (error) {
                    telemetry.emit({
                        pageAction: 'udf.export_viz',
                        source: 'actionMenu',
                        event: 'viz.export_failed',
                        metadata: { type },
                        error: error.message,
                    });
                }
            }, 500),
        [dashboardApi, itemId, type, telemetry, createToast]
    );

    if (itemId == null || dashboardApi == null || type === null) {
        return null;
    }
    return (
        <ActionButton
            data-test="export-button"
            onClick={handleOnClick}
            icon={
                <Download screenReaderText={screenReaderText || _('Export')} />
            }
        />
    );
};

export default ExportButton;
