import React, { memo, useCallback, useMemo, useContext } from 'react';
import { _ } from '@splunk/ui-utils/i18n';
import Search from '@splunk/dashboard-icons/Search';
import {
    replaceTokensForObject,
    hasTokensInObject,
} from '@splunk/dashboard-utils/token';
import { TelemetryContext } from '@splunk/dashboard-telemetry';
import type {
    DashboardApi,
    DataSourceDefinition,
} from '@splunk/dashboard-types';
import ActionButton from './ActionButton';
import { BaseButtonProps } from './BaseButton';

interface ResolvedSearchProps {
    dataSourceId: string;
    dataSourceDef: DataSourceDefinition;
    submittedTokens: Record<string, unknown>;
    dashboardApi: DashboardApi;
}

const resolvedSearch = ({
    dataSourceId,
    dataSourceDef,
    submittedTokens,
    dashboardApi,
}: ResolvedSearchProps) => {
    let { options } = dataSourceDef;
    if (dataSourceDef.type === 'ds.chain') {
        options = {
            ...options,
            ...dashboardApi.getCompleteSearchQueryAndParameters(dataSourceId),
        };
    }
    if (options) {
        const resolvedOptions = replaceTokensForObject(
            options,
            submittedTokens
        ) as typeof options;
        if (!hasTokensInObject(resolvedOptions)) {
            return {
                ...dataSourceDef,
                options: resolvedOptions,
            };
        }
    }
    return null;
};

const noop = () => undefined;

interface ActionButtonWrapperProps {
    search: unknown;
    onOpenSearchClick: (...args: unknown[]) => void;
    screenReaderText?: string;
    disabled: boolean;
}

const ActionButtonWrapper = memo(
    ({
        search,
        onOpenSearchClick = noop,
        screenReaderText,
        disabled,
    }: ActionButtonWrapperProps): JSX.Element => {
        const telemetry = useContext(TelemetryContext);
        const handleClick = useCallback(() => {
            telemetry.collect({
                source: 'actionMenu',
                event: 'openInSearch',
            });
            onOpenSearchClick(search);
        }, [search, onOpenSearchClick, telemetry]);

        const icon = useMemo(
            () => (
                <Search
                    screenReaderText={screenReaderText || _('Open in Search')}
                />
            ),
            [screenReaderText]
        );

        return (
            <ActionButton
                data-test="open-search-button"
                onClick={handleClick}
                icon={icon}
                disabled={disabled}
            />
        );
    }
);

interface OpenSearchButtonProps extends BaseButtonProps {
    onOpenSearchClick?: () => void;
    disabled?: (...args: unknown[]) => boolean;
}

const OpenSearchButton = ({
    itemId,
    dashboardApi,
    visualization,
    dataSources = {},
    submittedTokens = {},
    onOpenSearchClick = noop,
    screenReaderText,
    disabled,
}: OpenSearchButtonProps): JSX.Element | null => {
    const primary = visualization?.dataSources?.primary;
    const primaryDataSource = primary ? dataSources?.[primary] : null;
    if (
        itemId == null ||
        dashboardApi == null ||
        primaryDataSource == null ||
        !primary
    ) {
        return null;
    }
    // disable button if search is not resolved
    const resolved = resolvedSearch({
        dataSourceId: primary,
        dataSourceDef: primaryDataSource,
        submittedTokens,
        dashboardApi,
    });
    // Default behavior is to disable if the type is not ds.search, ds.chain, or ds.savedSearch, but app developers can override with their behavior
    const disableButton =
        typeof disabled === 'function'
            ? disabled(resolved)
            : resolved === null ||
              !(
                  resolved.type === 'ds.search' ||
                  resolved.type === 'ds.chain' ||
                  resolved.type === 'ds.savedSearch'
              );

    return (
        <ActionButtonWrapper
            search={resolved}
            onOpenSearchClick={onOpenSearchClick}
            screenReaderText={screenReaderText}
            disabled={disableButton}
        />
    );
};

export default OpenSearchButton;
