import React, { useContext, useCallback } from 'react';
import get from 'lodash/get';
import { _ } from '@splunk/ui-utils/i18n';
import { TelemetryContext } from '@splunk/dashboard-telemetry';
import Refresh from '@splunk/dashboard-icons/Refresh';
import ActionButton from './ActionButton';
import type { BaseButtonProps } from './BaseButton';

const RefreshButton = ({
    itemId,
    dashboardApi,
    screenReaderText = _('Refresh'),
    visualization,
}: BaseButtonProps): JSX.Element | null => {
    const telemetry = useContext(TelemetryContext);

    const handleOnClick = useCallback(() => {
        if (!dashboardApi || !itemId) {
            return;
        }
        // log telemetry here to determine amount of people using actionMenu vs keyboard shortcuts
        telemetry.collect({
            source: 'actionMenu',
            event: 'refresh',
        });
        dashboardApi.refreshVisualization(itemId);
    }, [telemetry, dashboardApi, itemId]);

    if (
        itemId == null ||
        dashboardApi == null ||
        !get(visualization, 'dataSources.primary')
    ) {
        return null;
    }
    return (
        <ActionButton
            data-test="refresh-button"
            onClick={handleOnClick}
            icon={<Refresh screenReaderText={screenReaderText} />}
        />
    );
};

export default RefreshButton;
