import React, { useContext, useCallback } from 'react';
import get from 'lodash/get';
import { _ } from '@splunk/ui-utils/i18n';
import { TelemetryContext } from '@splunk/dashboard-telemetry';
import Fullscreen from '@splunk/dashboard-icons/Fullscreen';
import ActionButton from './ActionButton';
import { BaseButtonProps } from './BaseButton';

const FullscreenButton = ({
    itemId,
    visualization,
    dashboardApi,
    screenReaderText,
}: BaseButtonProps): JSX.Element | null => {
    const telemetry = useContext(TelemetryContext);
    const type = get(visualization, 'type', null);
    const handleOnClick = useCallback(() => {
        if (!dashboardApi || !itemId) {
            return;
        }
        // log telemetry here to determine amount of people using actionMenu vs keyboard shortcuts
        telemetry.emit({
            pageAction: 'udf.fullscreen',
            source: 'actionMenu',
            event: 'viz.fullscreen',
            metadata: { type },
        });
        dashboardApi.toggleVisualizationFullscreen(itemId);
    }, [dashboardApi, telemetry, itemId, type]);

    if (
        itemId == null ||
        dashboardApi == null ||
        type === null ||
        type === 'viz.rectangle' ||
        type === 'viz.ellipse'
    ) {
        return null;
    }
    return (
        <ActionButton
            data-test="fullscreen-button"
            onClick={handleOnClick}
            icon={
                <Fullscreen
                    screenReaderText={screenReaderText || _('Fullscreen')}
                />
            }
        />
    );
};

export default FullscreenButton;
