import React, { memo, useCallback, useState, useRef, useContext } from 'react';
import { _ } from '@splunk/ui-utils/i18n';
import Popover from '@splunk/react-ui/Popover';
import Menu from '@splunk/react-ui/Menu';
import Layers from '@splunk/dashboard-icons/Layers';
import { TelemetryContext } from '@splunk/dashboard-telemetry';
import type { DashboardApi } from '@splunk/dashboard-types';
import ActionItem from './ActionItem';
import ActionButton from './ActionButton';
import { BaseButtonProps } from './BaseButton';

const menuStyle = { width: 130 };

interface MenuItemContainerProps {
    label: string;
    adjustOrder: (config: { from: number; to: number; event: string }) => void;
    from: number;
    to: number;
    dataTest: string;
}

const MenuItemContainer = memo(function MenuItemContainer({
    label,
    adjustOrder,
    from,
    to,
    dataTest,
}: MenuItemContainerProps): JSX.Element {
    const handleClick = useCallback(
        () => adjustOrder({ from, to, event: dataTest }),
        [adjustOrder, from, to, dataTest]
    );

    return (
        <Menu.Item data-test={dataTest} onClick={handleClick}>
            {label}
        </Menu.Item>
    );
});

/**
 *
 */
const LayerButton = ({
    dashboardApi,
    itemId,
    screenReaderText,
}: BaseButtonProps): JSX.Element | null => {
    const [open, setOpen] = useState(false);
    const anchor = useRef(null);
    const telemetry = useContext(TelemetryContext);

    const handleRequestClose = useCallback(() => {
        setOpen(false);
    }, []);

    const handleOpen = useCallback(() => {
        setOpen(true);
    }, []);

    const setRef = useCallback((refEl) => {
        anchor.current = refEl;
    }, []);

    const adjustOrder = useCallback(
        ({ from, to, event }) => {
            telemetry.collect({
                source: 'actionMenu',
                event,
            });
            // It's not possible to trigger this if dashboardApi is not defined, but typescript doesn't know that.
            (dashboardApi as DashboardApi).adjustVisualizationOrder(from, to);
            handleRequestClose();
        },
        [dashboardApi, handleRequestClose, telemetry]
    );

    if (!itemId || !dashboardApi) {
        return null;
    }

    if (dashboardApi.getDefinition().layout?.type !== 'absolute') {
        return null;
    }

    // dashboardApi is defined by the time we get here
    const order = dashboardApi.getVisualizationOrder();

    // do not display this button if no order or there's only one item
    if (order == null || order.length === 1) {
        return null;
    }

    const itemIdx = order.indexOf(itemId);
    const isFront = itemIdx === order.length - 1;
    const isBack = itemIdx === 0;
    return (
        <ActionItem>
            <ActionButton
                data-test="layer-button"
                icon={
                    <Layers
                        screenReaderText={screenReaderText || _('Layers')}
                    />
                }
                ref={setRef}
                onClick={handleOpen}
            />
            <Popover
                open={open}
                anchor={anchor.current}
                onRequestClose={handleRequestClose}
                defaultPlacement="right"
                appearance="normal"
            >
                <Menu style={menuStyle} data-test="layer-menu">
                    {!isFront && (
                        <MenuItemContainer
                            dataTest="layer-forward"
                            label={_('Move forward')}
                            adjustOrder={adjustOrder}
                            from={itemIdx}
                            to={itemIdx + 1}
                        />
                    )}
                    {!isFront && (
                        <MenuItemContainer
                            dataTest="layer-front"
                            label={_('Bring to front')}
                            adjustOrder={adjustOrder}
                            from={itemIdx}
                            to={order.length - 1}
                        />
                    )}
                    {!isBack && (
                        <MenuItemContainer
                            dataTest="layer-backward"
                            label={_('Move backward')}
                            adjustOrder={adjustOrder}
                            from={itemIdx}
                            to={itemIdx - 1}
                        />
                    )}
                    {!isBack && (
                        <MenuItemContainer
                            dataTest="layer-back"
                            label={_('Send to back')}
                            adjustOrder={adjustOrder}
                            from={itemIdx}
                            to={0}
                        />
                    )}
                </Menu>
            </Popover>
        </ActionItem>
    );
};

export default LayerButton;
